body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 15px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.title {
  text-align: center;
  color: #333;
}

.breed-select {
  cursor: pointer;
  width: 20%;
  color: #333;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 10px;
  font-size: 16px;
}

.loader_style {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #070808;
  border-color: #070808 #040403 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader_style:after, .loader_style:before {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  transform-origin: center;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #080806 #050506;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader_style:before {
  width: 32px;
  height: 32px;
  border-color: #030304 #090908 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation: rotation 1.5s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.loader {
  text-align: center;
  color: #030303 #010101;
  margin: 20px 0;
  font-weight: bold;
  display: none;
}

.cat-info {
  width: 800px;
  text-align: center;
  margin: 10px auto 0;
}

.image {
  max-width: 800px;
  border: 3px solid #010101;
  margin-bottom: 5px;
}

.name {
  color: #333;
  margin-bottom: 5px;
  font-size: 24px;
}

.description {
  color: #555;
  margin-bottom: 5px;
}

.temperament {
  color: #333;
}

